const findModifiers = require('./findModifiers')
const words = require('./data')
const isValid = require('./validate')
const parseDecimals = require('./parseDecimals')
const parseNumeric = require('./parseNumeric')
const parseFraction = require('./parseFractions')
const improperFraction = /^([0-9,\. ]+)\/([0-9,\. ]+)$/

//some numbers we know
const casualForms = {
  // 'a few': 3,
  'a couple': 2,
  'a dozen': 12,
  'two dozen': 24,
  zero: 0,
}

const isFractional = (term) => {
  return term !== 'a' && (!!words.fractions[term] || !!words.fractions[term.slice(0, -1)])
}

// a 'section' is something like 'fifty-nine thousand'
// turn a section into something we can add to - like 59000
const section_sum = (obj) => {
  return Object.keys(obj).reduce((sum, k) => {
    sum += obj[k]
    return sum
  }, 0)
}

//turn a string into a number
const parse = function (str, isFraction, depth = 0) {
  //convert some known-numbers
  if (casualForms.hasOwnProperty(str) === true) {
    return casualForms[str]
  }
  //'a/an' is 1
  if (str === 'a' || str === 'an') {
    return 1
  }
  const modifier = findModifiers(str)
  str = modifier.str
  let last_mult = null
  let has = {}
  let sum = 0
  let isNegative = false
  const terms = str.split(/[ -]/)
  // const isFraction = findFraction(terms)
  for (let i = 0; i < terms.length; i++) {
    let w = terms[i]
    w = parseNumeric(w)

    if (!w || w === 'and') {
      continue
    }
    if (w === '-' || w === 'negative') {
      isNegative = true
      continue
    }
    if (w.charAt(0) === '-') {
      isNegative = true
      w = w.substr(1)
    }

    //decimal mode
    if (w === 'point') {
      sum += section_sum(has)
      sum += parseDecimals(terms.slice(i + 1, terms.length))
      sum *= modifier.amount
      return sum
    }

    //improper fraction
    const fm = w.match(improperFraction)
    if (fm) {
      const num = parseFloat(fm[1].replace(/[, ]/g, ''))
      const denom = parseFloat(fm[2].replace(/[, ]/g, ''))
      if (denom) {
        sum += num / denom || 0
      }
      continue
    }

    if (isFraction && terms.length === 1 && isFractional(w)) {
      return parseFraction([terms[terms.length - 1]])
    }

    //prevent mismatched units, like 'seven eleven' if not a fraction
    if (isValid(w, has) === false || (isFraction && isFractional(w) && terms.length > 1)) {
      if (isFraction) {
        sum += section_sum(has)
        let fractional = parse(terms.slice(i).join(' '), isFraction, depth + 1)
        let prev = parse(terms[i - 1])
        if (
          sum === 0 ||
          terms[i - 1] === 'and' ||
          (terms[i - 2] === 'and' && terms[i - 1] === 'a')
        ) {
          sum += fractional
        } else if (prev > 19 && prev < 100) {
          sum = (1 / (sum + 1 / fractional)).toPrecision(4)
        } else {
          sum *= fractional
        }
        return sum
      } else {
        return null
      }
    }

    //buildOut section, collect 'has' values
    if (/^[0-9\.]+$/.test(w)) {
      has['ones'] = parseFloat(w) //not technically right
    } else if (words.ones.hasOwnProperty(w) === true) {
      has['ones'] = words.ones[w]
    } else if (words.teens.hasOwnProperty(w) === true) {
      has['teens'] = words.teens[w]
    } else if (words.tens.hasOwnProperty(w) === true) {
      has['tens'] = words.tens[w]
    } else if (words.multiples.hasOwnProperty(w) === true) {
      let mult = words.multiples[w]

      //something has gone wrong : 'two hundred five hundred'
      //possibly because it's a fraction
      if (mult === last_mult) {
        if (isFraction) {
          has = {}
          let fractional = parse(terms.slice(i - 1).join(' '), isFraction)
          sum += fractional
          return sum
        } else {
          return null
        }
      }
      //support 'hundred thousand'
      //this one is tricky..
      if (mult === 100 && terms[i + 1] !== undefined) {
        // has['hundreds']=
        const w2 = terms[i + 1]
        if (words.multiples[w2]) {
          mult *= words.multiples[w2] //hundredThousand/hundredMillion
          i += 1
        }
      }
      //natural order of things
      //five thousand, one hundred..
      if (last_mult === null || mult < last_mult) {
        sum += (section_sum(has) || 1) * mult
        last_mult = mult
        has = {}
      } else {
        //maybe hundred .. thousand
        sum += section_sum(has)
        last_mult = mult
        sum = (sum || 1) * mult
        has = {}
      }
    }
  }
  //dump the remaining has values
  sum += section_sum(has)
  //post-process add modifier
  sum *= modifier.amount
  sum *= isNegative ? -1 : 1
  //dont return 0, if it went straight-through
  if (sum === 0 && Object.keys(has).length === 0) {
    return null
  }
  return sum
}

module.exports = parse
